.unsplash__inner {
    ul {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
  
      li {
        width: 23%;
        position: relative;
  
        em {
          display: block;
          height: 80px;
          margin-bottom: 30px;
          font-family: var(--font-kor2);
        }
        .title {
          padding: 5px 0;
          display: inline-block;
        }
        .star {
          background: #fff;
          color: #000;
          position: absolute;
          left: 10px;
          top: 10px;
          width: 30px;
          height: 30px;
          border-radius: 100px;
          text-align: center;
          line-height: 30px;
          font-weight: 800;
        }
      }
    }
  
    a {
      color: var(--white);
    }
  }
  
  .unsplash__search {
    margin-bottom: 100px;
  
    .container {
      position: relative;
    }
  
    h2 {
      color: var(--white);
      font-size: 40px;
      text-indent: -9999px;
      height: 0;
    }
    input {
      background: #000;
      border: 1px solid var(--white);
      border-radius: 50px;
      color: #fff;
      width: 100%;
      padding: 14px 30px;
      font-family: var(—font-kor2);
    }
    button {
      position: absolute;
      right: 6px;
      top: 5px;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      border: 0;
      font-family: var(—font-kor2);
      cursor: pointer;
      z-index: 1000;
    }
  }
  